import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';

class Group extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			type,
			className,
			children,
			...others
		} = props;

		const klassName = KlassName(
			'group',
			{
				['group--' + type]:type
			},
			className
		);

		return (
			<div
				{...others}
				className={klassName}
			>
				{children}
			</div>
		);
	}
};

Group.propTypes = {
	type: PropTypes.oneOf(['button','note','card','warpper','dropdown']),
	className: PropTypes.string,
	children: PropTypes.node.isRequired
};

Group.defaultProps = {
};

export default Group;